.layout {
  display: grid;
  /* min-width: 100%; */
  min-height: 100%;
  margin: auto;
  grid-template-columns: 1fr;
  /* grid-auto-rows: minmax(100px, auto); */
  grid-template-rows: auto minmax(280px, auto) minmax(1fr, auto);
  grid-template-areas:
    "nav "
    "sr "
    "main ";
  grid-gap: 15px;
  /* border: 2px solid rgb(0, 238, 255); */
}

.nav {
  grid-area: nav;
  /* border: 2px solid blue; */
}

.srf {
  grid-area: sr;
  /* border: 2px solid red; */
}

.main {
  grid-area: main;
  /* border: 2px solid green; */
}

