.SearchBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-position: center;
  background-size: cover;

  background-image: url("https://scontent.fdac13-1.fna.fbcdn.net/v/t1.15752-9/114588285_973522823097561_1851128710708650496_n.png?_nc_cat=100&_nc_sid=b96e70&_nc_ohc=wj7nFyUPVUAAX_spP3D&_nc_ht=scontent.fdac13-1.fna&oh=91a13e81ee65ec730387c5ee84b9db51&oe=5F3EB96A");
  height: 15rem;
  background-repeat: no-repeat;
  padding-top: 80px;
  border-radius: 8px;
  
}

.SearchBar-sort-options ul {
  display: flex;
  justify-content: center;
  margin-bottom: 2.22rem;
  color: #ffffff;
}

.SearchBar-sort-options li {
  list-style: none;
  width: 4.33rem;
  border-bottom: 1px solid #fff;
  margin-right: 20px;
  line-height: 1.13em;
  text-align: center;
  font-weight: 600;
  font-size: 0.83rem;
  transition: color 0.25s;
  color: #c86d79;
}

.SearchBar-sort-options li:hover {
  color: #c86d79;
  border-color:  #c86d79;
  cursor: pointer;
}

.SearchBar-sort-options li.active,
.SearchBar-sort-options li.active:hover {
  color:  #c86d79;
  border-color:  #c86d79;
}
.SearchBar-fields {
  display: flex;
  justify-content: center;
  margin-bottom: 2.88rem;
  font-family: Arial, FontAwesome;
  cursor: pointer;
}

.SearchBar-fields input {
  width: 21rem;
  padding: 0.66rem 1rem;
  border: 1px solid #b6e2e1;
  border-radius: 4px;
  font-size: 0.77rem;
  font-weight: 500;
  background: #b6e2e1;
  cursor: text;
}

.SearchBar-fields input:last-child {
  margin-right: 0;
}

.SearchBar-submit {
  text-align: center;
  color: white;
}
.SearchBar-submit a {
  border-radius: 4px;
  padding: 0.72rem 1.7rem;
  background-color: #33807e;
  font-weight: 600;
  transition: background-color 0.5s;
}

.SearchBar-submit a:hover {
  cursor: pointer;
  background-color: #2c6d6c;
  color:white;
}
select#city {
  background: #b6e2e1;
}
select#category {
  background: #b6e2e1;
}

@media only screen and (max-width: 560px) {
  .SearchBar {
    background-image: none;
    background-color: #017374;
  }

  .SearchBar-sort-options ul {
    margin-left: 2rem;
    margin-right: 2rem;
    padding-bottom: 0;
  }

  .SearchBar-fields {
    flex-direction: column;
    align-items: center;
    margin-bottom: 0.2;
  }

  .SearchBar-fields input {
    margin-right: 0;
  }
}

