.food-card {
  background-color: #ffffff;
  /* margin: auto; */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  width: 240px;
  font-size: 1em;
  border-radius: 5px;
}

.food-card img {
  width: 100%;
  height: 150px;
}

.food-card h2 {
  font-size: 1.4em;
}

.food-card article {
  padding: 10px;
}
.slick-slider {
  margin: 10px;
}
.slick-slide {
  padding: 10px;
}
